$red: #F15854;
$gray: #4D4D4D;
$blue: #5DA5DA;
$yellow: #DECF3F;
$purple: #B276B2;
$white: #ffffff;
$textDefault: 0 0 1px black;
$centerAlignText: center;
$paddingDefault: 20px;
.padding {
  padding: $paddingDefault;
  &Double {
    padding: $paddingDefault * 2;
  }
  &Half {
    padding: $paddingDefault / 2;
  }
}

$widthDefault: 10%;
.width {
  width: $widthDefault;
  &Halv {
    width: $widthDefault * 5;
  }
  &Hel {
    width: $widthDefault * 10;
  }
}

.chart {
  background: conic-gradient(
          $gray 0 30%,
          $blue 0 50%,
          $yellow 0 70%,
          $purple 0 100%
  );
  border-radius: 50%;
  width: 50%;
  height: 0;
  padding-top: 50%;
  float: left;
  padding-right: 20px;
}

.key {
  width: 50%;
  float: right;
  list-style: none;
  display: table;
  border-collapse: separate;
   li {
    display: table-row;
     * {
      display: table-cell;
      border-bottom: 12px solid white;
    }
  }
}

.percent {
  color: $white;
  padding: 10px 2px;
  text-shadow: $textDefault;
  text-align: $centerAlignText;
}

.choice {
  padding-left: 10px;
}


.gray {
  background: $gray;
}

.blue {
  background: $blue;
}

.yellow {
  background: $yellow;
}

.purple {
  background: $purple;
}

* {
  box-sizing: border-box;
}

body {
  padding: 20px;
  font-family: sans-serif;
}